body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  max-width: 1000px;
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.Post {
  margin: 40px 0;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.Post {
  margin: 40px 0;
}


.Tab-box {
  width: 100%;
  height: 30px;
  position: fixed;
  bottom: 0;
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #eee;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.Tab-item {
  cursor: pointer;
}
.Tab-item.active {
  color: #748fab;
}
.Post-box {
  box-sizing: border-box;
  width: 100%;
  padding: 10px 20px;
  margin-top: 10px;
  border-bottom: 1px solid #eee;
  overflow: hidden;
}
.Post-box:last-child {
  border-bottom:  none;
}
.Post-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.Post-title {
  margin: 0;
  font-size: 16px;
  color: #666;
}
.Post-thumbnail {
  width: 100px;
  height: 70px;
  flex: none;
}
.Post-detail {
  margin-top: 10px;
  font-size: 12px;
  color: #999;
}
.Post-detail span {
  margin-right: 10px;
}
.Article {
  padding: 30px 20px;
}
.Article-title {
  font-size: 20px;
  margin: 0;
}
.detail {
  font-size: 16px;
  color: #999;
  margin-top: 0;
}
.Main-pic {
  width: 100%;
}
